import React, { createElement } from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movie({ year, title, summary, poster, genres, rating }){
    const max_length = 270;
    return (
        <div className = "movie">
            <img src = { poster } alt = { title } title = { title }></img>
            <div className = "movie__data">
                <h3 className = "movie__title_year">{ title } ({ year })</h3>
                <h5 className = "movie__genres">{ genres.join(', ') }</h5>
                <h5 className = "movie__rating">{ rating } / 10</h5>
                <p className = "movie__summary">{ summary.slice(0, max_length) }</p>
            </div>
        </div>
    );
}

Movie.propTypes = {
    id: PropTypes.number.isRequired,
    year: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    genres: PropTypes.array.isRequired,
    rating: PropTypes.number.isRequired,
}

export default Movie;